:root {
    --primary: rgb(246, 242, 239);
    --secondary: rgb(28, 32, 36);
  }
  
  .btn {
    padding: 8px 20px;
    outline: none;
    border: none;
    cursor: pointer;
  }
  
  .btn--primary {
    background-color: var(--primary);
    color: var(--secondary);
    border: 1px solid var(--primary);
  }
  
  .btn--outline {
    background-color: transparent;
    color: var(--primary);
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }

  .btn--outline--black {
    background-color: transparent;
    color: var(--secondary);
    padding: 8px 20px;
    border: 1px solid var(--secondary);
    transition: all 0.3s ease-out;
  }
 
  .btn--rounded{
    background-color: transparent;
    color: var(--primary);
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
    border-radius: 50px;
    margin-left: 1rem;
  }
  
  .btn--medium {
    padding: 8px 20px;
    font-size: 18px;
  }
  
  .btn--large {
    padding: 12px 26px;
    font-size: 20px;
  }
  
  .btn--large:hover,
  .btn--medium:hover {
    transition: all 0.3s ease-out;
    background: var(--primary);
    color: var(--secondary);
    transition: 250ms;
  }

  .btn--outline--black:hover {
    background-color: var(--secondary);
    color: var(--primary);
    padding: 8px 20px;
    border: 1px solid var(--secondary);
    transition: all 0.3s ease-out;
  }
